.introduce {
  position: relative;
  height: 100vh;

  display: flex;
  flex-direction: column;
  justify-content: center;

  background-image: linear-gradient(rgba(00,00,00,.1) 0%, rgba(00,00,00,.5) 100%),url("../img/bg-header.jpg");
  background-position: center;
  background-size: cover;
}

.introduce__title {
  font-size: 40px;
  color: #ffffff;
  text-align: center;
}

.introduce__subtitle {
  margin-top: 40px;
  padding: 0 20px;
  font-size: 14px;
  color: #ffffff;
  text-align: center;
}
