.help__wrapper {
  min-height: 440px;

  display: flex;
  flex-direction: column;
}

.help__title {
  padding: 60px 0;

  background-color: rgb(247, 237, 230);

  font-size: 30px;
  text-align: center;
}

.help__list {
  flex-grow: 1;

  display: flex;
  flex-direction: column;
  justify-content: space-around;

  text-align: center;
}

.help__item {
  padding: 20px;

  line-height: 20px;

  &:nth-child(2n - 1) {
    background-color: rgb(247, 237, 230);
  }
}

.help__strong {
  padding: 40px 20px;
  background-color: rgb(247, 237, 230);
}

.help__text {
  margin-bottom: 20px;

  display: inline-block;

  text-align: center;
  font-size: 18px;
  line-height: 26px;
}
