.about {
  padding: 60px 0 0;

  background-color: rgb(247, 237, 230);
}

.about__header {
  padding: 0 20px 60px;
}

.about__title {
  position: relative;
  padding-bottom: 16px;

  margin: 30px;

  font-size: 30px;
  line-height: 36px;
  text-align: center;

  &::before {
    content: "";
    position: absolute;
    bottom: 0;
    left: calc(50% - 25px);

    width: 50px;
    height: 3px;

    background-color: rgb(37, 157, 168);
  }
}

.about__description {
  margin-bottom: 30px;
  text-align: center;
  line-height: 23px;

  &--margin {
    margin-bottom: 0;
  }
}

.about__wrapper {
  padding: 60px 20px;

  background-image: url("../img/bg-page.jpg");
  background-repeat: no-repeat;
  background-position: center;
  background-size: cover;
}

.about__content {
  padding: 24px 20px;
  background-color: #ffffff;
}

.about__text {
  line-height: 22px;
}