.who {
  padding: 40px 20px;
}

.who__title {
  margin-bottom: 40px;

  font-size: 30px;
}

.who__item:last-child .who__text {
  margin-bottom: 0;
}

.who__subtitle {
  padding-left: 50px;

  background-image: url("../img/icon-check.svg");
  background-repeat: no-repeat;
  background-position: left center;
  background-size: 30px;
}

.who__text {
  margin-bottom: 40px;
  color: #444444;
}