.form__wrapper {
  padding: 40px 20px;
  background-color: rgb(247, 237, 230);
}

.form__title {
  margin-bottom: 40px;

  font-size: 30px;
  line-height: 36px;
  text-align: center;
}

.form__subtitle {
  font-size: 20px;
  text-align: center;
}

.form__fieldset {
  padding: 30px 20px 60px;

  min-height: 490px;

  display: flex;
  flex-direction: column;
  justify-content: space-between;
}

.form__input {
  margin: 0;
  padding: 0;

  width: 100%;
  height: 60px;

  border: none;
  border-bottom: 1px solid rgb(140, 140, 140);

  font-family: inherit;
  font-size: 16px;

  outline: none;

  &--textarea {
    height: 100px;
    resize: none;
  }
}

.form__button {
  margin: 0 auto;
  width: 280px;
}
