.navigation {
  width: 100%;
  background-color: rgb(247, 237, 230);
}

.navigation__wrapper {
  padding: 20px;
  background-color: #000000;

  display: flex;
  justify-content: space-between;
}

.navigation__logo {
  margin: 0;
  width: 100px;

  display: flex;
  align-items: center;

  color: #ffffff;
  font-weight: bold;
}

.navigation__button {
  padding: 0;
  border: none;
  font-size: 0;
  outline: none;

  width: 28px;
  height: 20px;

  background-color: transparent;
  background-image: url("../img/icon-close.svg");
  background-repeat: no-repeat;
  background-position: center;
}

.navigation__list {
  padding: 40px 0;
  width: 100%;
}

.navigation__item {
  padding: 10px 0;

  width: 100%;

  display: flex;
  align-items: center;
  justify-content: center;
}

.navigation__link {
  text-decoration: none;
  color: #000000;
  font-weight: bold;

  &--number {
    margin-top: 40px;
    font-size: 20px;
    font-weight: normal;
  }
}
